import { $get } from 'plow-js';
export const isAnalyzing = (state) => !!$get(['plugins', 'axeCoreView', 'analysisRequested'], state);
export const getReport = (state) => (contextPath) => $get(['plugins', 'axeCoreView', 'reports', contextPath], state);
export const isPoppedOut = (state) => $get(['plugins', 'axeCoreView', 'isPoppedOut'], state);
